import axios from "axios";

class axiosRequest {
  //get
  static searchStore(word) {
    return new Promise((resolve, reject) => {
      axios.get(`/api/store/search/${word}`)
      .then(response => {
        if (!response.data.error) {
          resolve(response.data.success);
        } else {
          reject(response.data.error);
        }
      })
      .catch(error => reject(error))
    })
  }

  static getSearchStore(id) {
    return new Promise((resolve, reject) => {
      axios.get(`/api/store/search/info/${id}`)
      .then(response => {
        resolve(response.data.success)
      })
      .catch(error => reject(error))
    })
  }

  //post
  static addStore(formData) {
    return new Promise((resolve, reject) => {
        axios.post("/api/store/add", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          }
        })
        .then(response => {
          if (!response.data.error) {
            resolve(response.data.success);
          } else {
            reject(response.data.error);
          }
        })
        .catch(err => reject(err));
    });
  };

  static postAvatar(formData) {
    return new Promise((resolve, reject) => {
      axios
        .post("/api/user/avatar", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          if (!response.data.error) {
            resolve(response.data.success);
          } else {
            reject(response.data.error);
          }
        })
        .catch(error => console.log(error));
    });
  }

  //patch
  static patchPassword(formData) {
    return new Promise((resolve, reject) => {
      axios
        .patch("/api/user/password", formData)
        .then(response => {
          if (!response.data.error) {
            resolve(response.data.success);
          } else {
            reject(response.data.error);
          }
        })
        .catch(err => reject(err));
    });
  };

  static patchDeleteAvatar() {
    return new Promise((resolve, reject) => {
      axios
        .patch("/api/user/avatar")
        .then(response => {
          if (!response.data.error) {
            resolve(response.data.success);
          } else {
            reject(response.data.error);
          }
        })
        .catch(error => console.log(error));
    });
  };
  //delete
  static deleteUser(formData) {
    return new Promise((resolve, reject) => {
      axios
        .delete(`/api/user/delete/${formData}`)
        .then(response => {
          if (!response.data.error) {
            resolve(response.data.redirect);
          } else {
            reject(response.data.error);
          }
        })
        .catch(error => console.log(error));
    });
  };
}

export default axiosRequest;
