<template>
  <section>
    <b-field>
      <b-autocomplete
        v-model="name"
        placeholder="L'Auberge..."
        :keep-first="keepFirst"
        :open-on-focus="openOnFocus"
        :data="filteredDataObj"
        field="name"
        @select="option => test(option.id)"
      > <template slot="empty">No results found</template> </b-autocomplete>
    </b-field>
  </section>
</template>

<script>
import axiosRequest from "../axiosRequest";
export default {
  data() {
    return {
      data: [],
      keepFirst: false,
      openOnFocus: false,
      name: "",
      selected: null
    };
  },
  methods: {
    test(e) {
      this.$router.push({ path: `/store/${e}` })
    }
  },
  computed: {
    filteredDataObj() {
      return this.data.filter(option => {
        return option.name
      });
    }
  },
  watch: {
    selected: function(val) {
      if (val !== null) {
        // axiosRequest.searchStore(val)
        // .then(response => )
        console.log(val);
      }
    },
    name(val) {
      if (val !== null) {
        axiosRequest
          .searchStore(val)
          .then(response => {
            for (let store of response) {
              console.log(store);
              this.data.push(store);
            }
          })
          .catch(error => console.log(error));
      }
    }
  }
};
</script>

<style>
#field {
  width: 200px;
}
</style>
