<script>
export default {
  data() {
    return {
      tags: {
        wifi: "",
        groupes: ""
      },
      array: []
    };
  },
  methods: {
    selectTags(e) {
      let tag = "";
      e.target.textContent
        ? (tag = e.target.textContent)
        : (tag = e.target.nextSibling.nextElementSibling.textContent);
      this.array.includes(tag)
        ? this.array.splice(this.array.indexOf(tag), 1)
        : this.array.push(tag);
      this.$router.push("/")
    }
  },
  watch: {
    array(val) {
      console.log(val);
    }
  }
};
