import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import router from "./router";
import { promises } from "fs";
// import { userInfo } from "os";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isConnected: null,
    user: {},
    avatarKey : 0,
    stores : [],
    reloadStores : false,
    page : 1
  },
  getters: {
    isConnected: state => {
      return state.isConnected;
    },
    userInfo(state) {
      return state.user;
    },
    stores(state) {
      return state.stores
    },
    page(state) {
      return state.page
    },
  },
  mutations: {
    isConnected(state) {
      state.isConnected = true;
    },
    logout(state) {
      state.isConnected = false;
    },
    userInfo(state, element) {
      state.user = element;
    },
    updateInfo(state, element) {
      Object.assign(state.user, element)
    },
    updateStores(state, element) {
      element.forEach(store => {
        state.stores.push(store)
      });
    },
    page(state) {
      state.page += 1;
    },
  },
  actions: {
    signup({ dispatch, commit }, formData) {
      return new Promise((resolve, reject) => {
        axios
        .post("/api/user/inscription", formData)
        .then(response => {
          if (response.data.redirectUrl) {
            resolve(`Bienvenue ${response.data.name} !`)
            commit('isConnected')
            dispatch('getInfo')
          } else {
            reject(response.data.error);
          }
        })
        .catch(err => console.log(err));
      })
    },
    login({ commit }, formData) {
      return new Promise((resolve, reject) => {
        axios
        .post("/api/user/login", formData)
        .then(response => {
          if (response.data.redirectUrl) {
            commit("isConnected");
            commit("userInfo", response.data);
            resolve(`Bienvenu ${response.data.name} !`)
            router.replace(`${response.data.redirectUrl}`);
          } else {
            reject(response.data.error);
          }
        })
        .catch(err => console.log(err));
      })
    },
    logout({ commit }) {
      localStorage.removeItem("cookies");
      axios
        .get("/api/user/logout")
        .then(resp => {
          commit("logout");
        })
        .catch(err => console.log(err));
    },
    getInfo({ commit }) {
      axios
        .get("/api/user/info")
        .then(response => {
          if (!response.data.error) {
            commit("userInfo", response.data);
          } else {
            commit("error", response.data.error);
          }
        })
        .catch(error => console.log(error));
    },
    patchInfo( { dispatch }, formData) {
      return new Promise((resolve, reject) => {
        axios
        .patch("/api/user/info", formData)
        .then(response => {
          if (!response.data.error) {
            // formData.password ? delete formData.password : ''
            // commit("updateInfo", formData);
            dispatch("getInfo");
            resolve(response.data.success);
          } else {
            reject(response.data.error)
          }
        })
        .catch(error => console.log(error));
      })
    },
    storeInfo({ commit }, page) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/api/store/info/${page}`)
          .then(response => {
            if (!response.data.error) {
              commit('updateStores', response.data.success);
              resolve("ok");
            } else {
              reject(response.data.error);
            }
          })
          .catch(error => reject(error));
      });
    }
  }
});
